* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  position: relative;
}

.search-form {
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
}

.search-form__input {
  width: 300px;
  border-radius: 10px;
  outline: none;
  margin-right: 20px;
  padding: 15px;
  font-size: 20px;
  box-shadow: 2px 1px 2px #478c9d;
}

.search-form__button {
  background-color: #00763f;
  border: none;
  border-radius: 10px;
  padding: 15px 30px;
  font-size: 20px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  box-shadow: 2px 1px 2px #021c02;
}

.load-more {
  color: #000;
  cursor: pointer;
  background-color: #6dcaad;
  background-clip: padding-box;
  border: none;
  border-radius: 6px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  font-size: 20px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  flex-direction: column;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo-card:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.photo-img {
  object-fit: cover;
  width: 320px;
  height: 213px;
  flex-grow: 1;
  display: block;
}

.photo-img :hover {
  cursor: zoom-in;
}

.photo-link {
  height: 100%;
  text-decoration: none;
  display: block;
}

